<template>
    <div>
        <JwtLoader v-if="loading" />

        <v-container v-else>
            <v-layout>
                <v-row
                class="d-flex justify-center"
                >
                <v-col
                class="ma-2"
                v-for="(game, i) in games"
                :key="game + i"
                cols="3"
                >
                <v-card 
                    height="200px"
                >    
                    <v-card-title>
                        Card title
                    </v-card-title>

                    <v-card-subtitle>
                        {{ game.Name }}
                    </v-card-subtitle>

                    <v-card-text>
                        {{ game.Price }}
                    </v-card-text>
                </v-card>
                </v-col>
            </v-row>
            </v-layout>
        </v-container>
        
        <!-- <v-btn @click="getAllGames">GAMES</v-btn> -->
    </div>
</template>

<script>
// import axios from 'axios'
import JwtLoader from '@/components/JwtLoader'
import api from '@/api';

export default {
components: {
    JwtLoader
},
data() {
    return {
        loading: true,
        games: []
    }
},
methods: {
    async getAllGames() {
        try {
            this.loading = true
            // const token = this.$store.state.auth.userInfo.token
            // const response = await axios.get(`https://jwt-learn-c0174-default-rtdb.europe-west1.firebasedatabase.app/games.json`)



            // const response = await api.get(`https://jwt-learn-c0174-default-rtdb.europe-west1.firebasedatabase.app/games.json`);


            const response = await api.get('/api/GetGames');



            // const response = await axios.get(`https://jwt-learn-c0174-default-rtdb.europe-west1.firebasedatabase.app/games.json?auth=${token}`)
            this.games = JSON.parse(response.data)
        } catch (error) {
            this.$store.state.auth.userInfo.token = ''
            this.$router.push('/')
            console.log(error)
        } finally {
            this.loading = false
        }
    }
},
computed: {

},
mounted() {
    this.getAllGames()
},
}
</script>

